import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {AdsDto} from '../models/ads.dto';

@Injectable({
  providedIn: 'root'
})
export class AdsService {
  base = '/api/ads';

  constructor(private readonly  http: HttpClient) {
  }

  create(ads: AdsDto) {
    return this.http.post<{ ads: AdsDto }>(this.base, ads);
  }

  getAll(params: {
    skip?: string,
    limit?: string
  }) {
    return this.http.get<{ ads: AdsDto[], count: number }>(this.base, {params});
  }

  update(id: string, ads: AdsDto) {
    return this.http.put<{ ads: AdsDto }>(`${this.base}/${id}`, ads);
  }

  delete(id: string) {
    return this.http.delete<{ ads: AdsDto }>(`${this.base}/${id}`);
  }

}
