import {Component, Inject, OnInit} from '@angular/core';
import {AdsDto} from 'src/app/models/ads.dto';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';
import {AdsModifyComponent} from '../ads-modify/ads-modify.component';
import {AdsService} from '../../../services/ads.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import {map} from 'rxjs/operators';
import {DialogComponent} from '../../../components/dialog/dialog.component';
import {FormControl, Validators} from '@angular/forms';
import {environment} from 'src/environments/environment';

@Component({
  selector: 'app-ads',
  templateUrl: './ads.component.html',
  styleUrls: ['./ads.component.scss'],
})
export class AdsComponent implements OnInit {
  limit: number;
  skip: number;
  count: number;
  displayedColumns: string[] = [
    'index',
    'type',
    'active',
    'iran',
    'place',
    'createdAt',
    'action',
  ];
  ads: AdsDto[];

  constructor(
    private readonly dialog: MatDialog,
    private readonly adsService: AdsService,
    private readonly snackBar: MatSnackBar
  ) {
    this.limit = 10;
    this.skip = 0;
    this.count = 0;
    this.ads = [];
  }

  ngOnInit(): void {
    this.getAll();
  }

  _openSnackBar(message: string) {
    this.snackBar.open(message, null, {
      horizontalPosition: 'left',
      verticalPosition: 'top',
      direction: 'rtl',
      duration: 5000
    });
  }

  // بازکزدن دیالوگ ایجاد تبلیغ
  openCreateDialog() {
    this.dialog.open(AdsModifyComponent, {
      width: '1000px',
      data: {
        ads: {
          // به صورت دیفالت ایران باشد
          iran: true,
          // به صورت دیفالت فعال باشد
          active: true,
        },
        state: 'create',
      },
    }).afterClosed().subscribe((res) => {
      console.log(res);
      if (res !== null && typeof res !== 'undefined') {
        this.adsService.create(res).subscribe((resp) => {
          this._openSnackBar('تبلیغ با موفقیت ایجاد شد');
          this.resetPage();
        }, (err) => {
          console.log(err);
        });
      }
    });
  }

  // بازکردن دیالوگ ویرایش
  openEditDialog(ads: AdsDto) {
    this.dialog.open(AdsModifyComponent, {
      width: '1000px',
      data: {ads, state: 'edit'}
    }).afterClosed().subscribe((res) => {
      if (res !== null && res !== 'undefined') {
        this.adsService.update(res._id, res).subscribe((resp) => {
            this._openSnackBar('تبلیغ با موفقیت ویرایش شد');
            this.resetPage();
          }, (err) => {
            console.log(err);
          }
        );
      }
    });
  }

  // باز کردن دیالوگ حذف
  openDeleteDialog(ads: AdsDto) {
    this.dialog.open(DialogComponent, {
      width: '250px',
      data: {title: 'حذف تبلیغ', content: 'آیا مطمئن هستید'}
    }).afterClosed().subscribe((res) => {
      if (res) {
        this.adsService.delete(ads._id).subscribe((resp) => {
          this._openSnackBar('حذف با موفیت انجام شد');
          this.resetPage();
        }, (err) => {
          console.log(err);
        });
      }
    });
  }

  // باز کردن پیش نمایش تبلیغ
  openPreview(ads: AdsDto) {
    if (ads.type === 'banner') {
      this.dialog.open(AdsDialog, {
        data: {image: ads.image}
      });
    } else {
      // پاپ آپ
      const newwindow = window.open(ads.link, 'bottom', 'height=600,width=600');
      if (window.focus) {
        newwindow.focus();
      }
    }
  }

  // باز گزداندن صفحه به حالت اول
  resetPage() {
    this.skip = 0;
    this.limit = 10;
    this.getAll();
  }

  // گرفتن لیست تبلیغات
  async getAll() {
    try {
      await this.adsService.getAll({
        skip: this.skip.toString(),
        limit: this.limit.toString()
      }).pipe(map(res => {
        this.ads = res.ads;
        this.count = res.count;
      })).toPromise();

    } catch (error) {
      console.log(error);
    }
  }


  // صفحه بعد
  nextPage() {
    if (this.skip + this.limit < this.count) {
      this.skip += this.limit;
      this.getAll();
    } else {
      this.skip = this.count - this.limit;
      this.getAll();
    }
  }

  // صفحه قبل
  prevPage() {
    if (this.skip - this.limit > 0) {
      this.skip -= this.limit;
      this.getAll();
    } else {
      this.skip = 0;
      this.getAll();
    }
  }

  // صفحه اول
  firstPage() {
    this.skip = 0;
    this.getAll();
  }

  // صفحه آخر
  lastPage() {
    this.skip = this.count - this.limit;
    this.getAll();
  }
}

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'ads.dialog',
  templateUrl: 'ads.dialog.html',
  styleUrls: ['./ads.component.scss'],
})
// tslint:disable-next-line: component-class-suffix
export class AdsDialog {
  base: string;

  constructor(@Inject(MAT_DIALOG_DATA) public data: { image: string }) {
    this.base = environment.url;
  }

}
