import {Component, OnInit} from '@angular/core';
import {TicketService} from 'src/app/services/ticket.service';
import {TicketDto, TicketResponse} from 'src/app/models/ticket.dto';
import {ActivatedRoute, Router} from '@angular/router';
import {map} from 'rxjs/operators';
import {FormControl, Validators} from '@angular/forms';

@Component({
  selector: 'app-ticket-detail',
  templateUrl: './ticket-detail.component.html',
  styleUrls: ['./ticket-detail.component.scss'],
})
export class TicketDetailComponent implements OnInit {
  ticket: TicketDto = {};
  id: string;
  ticketResponse: TicketResponse;
  subjectController: FormControl;
  textController: FormControl;

  constructor(
    private readonly ticketService: TicketService,
    private readonly route: ActivatedRoute,
    private readonly router: Router
  ) {
    this.route.paramMap.subscribe((params) => {
      this.id = params.get('id');
      this.getTicket(this.id);
    });
    this.ticketResponse = {};
  }

  ngOnInit(): void {
    this.subjectController = new FormControl('', [
      Validators.required
    ]);
    this.textController = new FormControl('', [
      Validators.required
    ]);
  }

  // خوتنده شدن تیکت
  async readTicket() {
    try {
      await this.ticketService
        .readTicket(this.id)
        .pipe(
          map((res) => {
            this.ticket = res.ticket;
          })
        )
        .toPromise();
    } catch (error) {
      console.log(error);
    }
  }

  // جواب دادن به کاربر به وسیله ایمیل
  sendResponse() {
    if (this.subjectController.valid && this.textController.valid) {
      this.ticketService.sendResponse(this.ticketResponse).subscribe(res => {
        this.router.navigate(['/admin/dashboard/tickets']);
      });
    }
  }

  // گرفتن تیکت با آی دی
  getTicket(id: string) {
    try {
      this.ticketService.getTicketById(id).subscribe((res) => {
        this.ticket = res.ticket;
        this.ticketResponse.email = res.ticket.email;
      });
    } catch (error) {
      console.log(error);
    }
  }
}
